.list {
  padding: 0;

  font-weight: 500;
}
.link {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  border: 2px solid blue;
  border-radius: 8px;
}
.name {
  font-weight: 800;
  margin-left: 15px;
  margin-right: 15px;
  color: rgb(13, 78, 232);
}
.title {
  margin-left: 15px;
  font-weight: 600;
}
.btn {
  margin-left: 20px;
  text-align: center;
  text-decoration: none;
  border: none;
  font-weight: 600;
  color: #fff;
  background: rgb(109, 147, 235);
  padding: 5px 10px;
  border-radius: 5px;
}
.btn:hover,
.btn:focus {
  background: rgb(88, 229, 124);
}
